  }
  .msgTitle{
     font-size: 1.1em;
     color: #000;
     padding-bottom: 10px;
  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .msgContent{
     color: #666;
     opacity: 0.8;
     position: relative;
  }
  .msgContent span{
     display: inline-block;
     padding-left: 0.3rem;
  }
  .notices .icon-xiaoxi{
     position: absolute;
     display: block;
     width: 0.2rem;
     height: 0.2rem;
     background-image: url("./xiaoxi.png");
     background-size: 100% 100%;
  }
  .msg_btn{
    padding: 0px 20px;
    height: 26px;
