  }
  .sign{
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,182,10,0.8)), to(#ff6a01));
    color: #fff;
    border:none;
  }
  .msg_sign{
    padding-top: 10px;overflow:hidden;
  }
  .msg_sign span label{
    color: #666;
  }
  .msg_sign span i{
     display: inline-block;
     width: 0.2rem;
     height:0.2rem;
     background-image: url("./sign.png");
     background-size: 100% 100%;
     position: relative;
     top:3px;
     margin-right: 0.05rem;
  }
  .message{
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(0,0,0,.6);
  }
  .message-box{
    position: absolute;
    top: 30%;
    left: 10%;
    z-index: 9999999999999999;
    width: 80%;
    box-sizing: border-box;
    padding: 0 10px;
    height: 40vh;
    border-radius: 0.06rem;
    background-color: #fff;
    overflow: hidden;
  }
  .radio{
    display: block;
    position: relative;
    padding: 10px 0;
    background-image:linear-gradient(180deg, #f0f0f0, #f0f0f0 50%, transparent 50%);
    background-size: 100% 1px;
    background-repeat: no-repeat;
    background-position:bottom;
  }
  .radio input{
    position: absolute;
    z-index: 1;
    width: 0.24rem;
    height: 0.24rem;
    opacity: 0;
  }
  .radio input+i {
    z-index: 0;
    display: inline-block;
    width: 0.24rem;
    height: 0.24rem;
    border-radius: 50%;
    vertical-align:top;
    background-color: #bab9b8;
  }
  .radio label{
    display: inline-block;
    padding-left: 0.06rem;
    width: 84%;
    vertical-align: middle;
    line-height: 0.24rem;
    color: #000;
  }
  .radio span{
    float: right;
    margin-right: 10px;
    vertical-align: middle;
    color: #666;
  }
  .radio input+i:after{
    content: "";
    position: absolute;
    left: 0.05rem;
    top:0.16rem;
    width: 0.09rem;
    height: 0.04rem;
    border: 0.024rem solid #fff;
    border-top-color: transparent;
    border-right-color: transparent;
    -ms-transform: rotate(-60deg);
    -moz-transform: rotate(-60deg);
    -webkit-transform: rotate(-60deg);
    transform: rotate(-45deg);
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
    .radio input+i:after{
      top: 0.15rem;
    }
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3.5), only screen and (min-device-pixel-ratio: 3.5) {
    .radio input+i:after{
      top: 0.16rem;
    }
  }
  .radio input:checked+i{
    background-color: #47c560;
    background:-webkit-gradient(linear, 0 0, right bottom, from(rgba(86,203,104,.8)), to(#28b74d));
  }
  .message-box .title{
    line-height: 0.4rem;
    color: #000;
    text-align: center;
    font-size: 0.16rem;
    background-image:linear-gradient(180deg, #f0f0f0, #f0f0f0 50%, transparent 50%);
    background-size: 100% 1px;
    background-repeat: no-repeat;
    background-position:bottom;
  }
  .message-box .radio-box{
     width: 90%;
     margin: 0 auto;
     height: calc(100% - 0.6rem - 0.4rem);
     overflow-y: auto;
     -webkit-overflow-scrolling: touch;
  }
  .message-box .buttons{
    padding-top: 0.16rem;
    height: 0.5rem;
  }
  .message-box .buttons button{
    border-radius: 0.24rem;
    width: 36%;
  }
  .message-box .buttons button:nth-child(2){
    float: right;
    margin-right: 10%;
  }
  .message-box .buttons button:nth-child(1){
    margin-left: 10%;
  }
  .hdBody{height: calc(100vh - 60px - 41px);}
</style>
<style>
  .msgBody .buttons button.mint-button{
    height: 0.3rem !important;
  }
</style>
<template>
  <!--<transition name="slide">-->
  <div class="msgBody">
    <mt-header title="团队签到">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="no-data-pub" v-if="noticeList.length<=0">
      <i class="iconfont  icon-zanwuxiaoxi"></i>
      <p class="text">暂无团队签到信息</p>
      <p class="sub-text">发起签到时请点下面按钮</p>
    </div>
    <div class="notices hdBody" v-else>
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <div class="box" @click="clickList(idx)" v-for="(notice,idx) in noticeList">
             <div><label>{{notice.createTime|FormartDate}}</label></div>
             <div class="msgTitle">
               {{notice.content}}
             </div>
             <div class="msg_sign">
                <span>发起人：{{notice.createName}}</span>
                <mt-button size="small" type="primary" class="msg_btn sign" v-if="!notice.sign" @click.stop="signClick(idx)">签到</mt-button>
                <mt-button size="small" type="primary" class="msg_btn" v-else @click="signToDetail(idx)">已签</mt-button>
             </div>
             <span class="red-point" v-if="notice.readed !== true"></span>
          </div>
        </div>
        </mt-loadmore>
    </div>
    <div class="send-btn">
        <mt-button type="primary"  size="large" @click="sendNotice">发送团队签到</mt-button>
    </div>
    <div class="message" id="messageId"  style="display: none;">
      <div class="message-box">
        <div class="title">选择团队</div>
        <div class="radio-box">
          <span class="radio" @click.stop="selectTeam(idx)" v-for="(team,idx) in teamList">
            <input type="radio" name="radio" :id="'radio'+idx">
            <i></i>
            <label :for="'radio'+idx">{{team.name}}</label>
          </span>
        </div>
        <div class="buttons">
          <mt-button type="primary" @click="clickOk">确 定</mt-button>
          <mt-button type="default" @click="clickCancel">取 消</mt-button>
        </div>
      </div>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  export default {
    name: 'teamNotice',
    data () {
      return {
         pageNum:1,
         pageSize:20,
         allLoaded:false,
         noticeList:[],
         teamList:[],
         formData:{
           groupId:'',
           groupName:'',
           userId:'',
           userName:'',
           latitude:'',
           longitude:'',
           address:''
         }
      }
    },
    components:[ Loadmore ],
    activated:function(){
      this.pageNum = 1;
      this.queryList();
      this.queryTeam();
    },
    created:function(){
       appMethod = this.getLocation;
       window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getAddress';
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        //this.$router.push('/home');
        this.$router.goBack();
      },
      clickList:function(idx){
        let sign = this.noticeList[idx];
        this.$router.push('/home/teamNoticeSignInfo?noticeId='+sign.id+'&groupId='+sign.toGroupId);
      },
      queryList:function(){//查询团队通知列
         let _this = this;
//         this.$axios({
//            method: 'post',
//            url: '/sign/PageListOfApp',
//            data:this.$qs.stringify({
//               userId:LoginUser.getUser().userId,
//               pageNum:this.pageNum,
//               pageSize:this.pageSize
//            })
//         }).then(function (response) {
            let response = {"code":1,"msg":"success","data":{"total":2,"list":[
              {"id":6,"fromUserId":"","toGroupId":8,"content":"我发起了签到请求","createDate":1503477292000,"createName":"杨勇冠","readed":true,"createTime":"2017-08-23 16:34:52","groupName":"我","sign":false},
              {"id":5,"fromUserId":"","toGroupId":8,"content":"我发起了签到请求","createDate":1503477292000,"createName":"杨勇冠","readed":true,"createTime":"2017-08-23 16:34:52","groupName":"我","sign":false},
              {"id":4,"fromUserId":"","toGroupId":8,"content":"我发起了签到请求","createDate":1503477292000,"createName":"杨勇冠","readed":true,"createTime":"2017-08-23 16:34:52","groupName":"我","sign":true},
              {"id":3,"fromUserId":"","toGroupId":8,"content":"我发起了签到请求","createDate":1503477292000,"createName":"杨勇冠","readed":true,"createTime":"2017-08-23 16:34:52","groupName":"我","sign":true},
              {"id":2,"fromUserId":"","toGroupId":8,"content":"我发起了签到请求","createDate":1503477292000,"createName":"杨勇冠","readed":true,"createTime":"2017-08-23 16:34:52","groupName":"我","sign":true},
              {"id":1,"fromUserId":"","toGroupId":8,"content":"我发起了签到请求","createDate":1503476815000,"createName":"杨勇冠","readed":true,"createTime":"2017-08-23 16:26:55","groupName":"我","sign":true}
            ],"pageNum":1,"pageSize":20,"pages":1,"size":2}};
            if(response.msg=='success'){
              if(!response.data.list||response.data.list.length<=0){
                _this.allLoaded = true;
              }
              if(_this.pageNum===1){
                _this.noticeList = response.data.list;
              }else{
                _this.noticeList = _this.noticeList.concat(response.data.list);
              }
            }
            if(!_this.$refs.loadmore) return;
            _this.$refs.loadmore.onBottomLoaded();
            _this.postion();
         //});
      },
      queryTeam:function(){
        let _this = this;
//        this.$axios({
//          method: 'post',
//          url: 'group/getGroupInfoList',
//          data:this.$qs.stringify({
//            userId:LoginUser.getUser().userId,
//            pageNum:0,
//            pageSize:1000
//          })
//        }).then(function (response) {
          let response = {"code":1,"msg":"success","data":{"total":1,"list":[
            {"id":8,"name":"团队1","readed":true,"noticeId":24},
            {"id":7,"name":"团队2","readed":true,"noticeId":24},
            {"id":6,"name":"团队3","readed":true,"noticeId":24},
            {"id":5,"name":"团队4","readed":true,"noticeId":24}
          ],"pageNum":0,"pageSize":1000,"pages":1,"size":1}};
          if(response.msg=='success'){
            _this.teamList = response.data.list;
          }
       // });
      },
      getLocation:function(data){
        this.formData.address = data.data.LocationName;
        this.formData.latitude = data.data.Latitude;
        this.formData.longitude = data.data.Longitude;
      },
      selectTeam:function(idx){
        this.formData.groupId = this.teamList[idx].id;
        this.formData.groupName = this.teamList[idx].name;
      },
      sendNotice:function(){
        document.getElementById('messageId').style.display = 'block';
      },
      clickCancel:function(){
        document.getElementById('messageId').style.display = 'none';
      },
      clickOk:function(){
        if(this.formData.groupId === ''){
          let instance = Toast("请选择团队");
          setTimeout(() => {
              instance.close();
          }, 1000);
          return;
        }
        let _this = this;
        document.getElementById('messageId').style.display = 'none';
        this.$axios({
          method: 'post',
          url: 'sign/insertGroupSign',
          data:this.$qs.stringify(this.formData)
        }).then(function (response) {
            if(response.data.code > 0){
              let instance = Toast("发布团队签到成功");
              setTimeout(() => {
                instance.close();
              }, 2000);
              _this.queryList();
            }

        });
      },
      signToDetail:function(idx){
        //解决点击签到之后点击已签不能进入详情页的bug，不能删除此方法
      },
      signClick:function(idx){//签到
        this.formData.groupId = this.noticeList[idx].toGroupId;
        this.formData.groupName = this.noticeList[idx].groupName;
        this.formData.noticeId = this.noticeList[idx].id;
        let _this = this;
        this.$axios({
          method: 'post',
          url: '/sign/sign',
          data:this.$qs.stringify(this.formData)
        }).then(function (response) {
          if(response.data.msg=='success'){
            let instance = Toast('签到成功');
            _this.noticeList[idx].sign = true;
            setTimeout(() => {
              instance.close();
            }, 2000);
          }
        });

      }
    }
  }
</script>




