opolitan Police)确认，警察已经对博罗市场持刀伤人的消息作出反应，并且在随后数次开枪。两处现场都报告有人伤亡，初步信息显示，有数人死亡，另有超过20人受伤。关于凶手身份的细节信息还没有立即披露。英国政府已经宣布此次事件为恐怖袭击；但是，同一时间邻近的沃克斯豪尔区(Vauxhall area)报告的持刀伤人事件已经被证明与这次恐怖袭击无关。<br/>联系方式：<br/>所属国家：英国<br/>当地报警电话：112或999<br/>当地大使馆电话：+44-020-76311430<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502963888000,"ftype":"","freaded":"","noticeId":"","title":"6月3日至4日英国伦敦车辆伤人事件(第2次更新)","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1438","category":"安保","createDate":"2017-08-17 17:58:08"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法机构驳回修宪案后，总统以欺诈的方式通过公投批准了修宪案。反对派成员深恐总统会再次修宪，不断巩固自身权力。现行宪法不允许阿齐兹在2019年期满后再次竞选。现任总统阿齐兹在2008年军事政变中胜出上台，2009年在总统选举中获胜，2014年在反对党抵制中再次当选。建议因存在暴力风险，请避开抗议活动。在集会地点周边时，请保持高度警惕。如果周围出现骚乱，请即离开躲避。出行前请了解交通状况，城区出行时请预留充足时间。<br/>联系方式：<br/>所属国家：毛里塔尼亚<br/>当地报警电话：17<br/>当地大使馆电话：00222-46586507、45252070<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502963856000,"ftype":"","freaded":"","noticeId":"","title":"毛里塔尼亚可能爆发全国性抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1437","category":"安保","createDate":"2017-08-17 17:57:36"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批