，初步信息显示，有数人死亡，另有超过20人受伤。关于凶手身份的细节信息还没有立即披露。英国政府已经宣布此次事件为恐怖袭击；但是，同一时间邻近的沃克斯豪尔区(Vauxhall area)报告的持刀伤人事件已经被证明与这次恐怖袭击无关。<br/>联系方式：<br/>所属国家：英国<br/>当地报警电话：112或999<br/>当地大使馆电话：+44-020-76311430<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502962951000,"ftype":"","freaded":"","noticeId":"","title":"6月3日至4日英国伦敦车辆伤人事件(第2次更新)","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1434","category":"安保","createDate":"2017-08-17 17:42:31"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述肯尼亚反对派“国家超级联盟”号召8月14日举行全国范围内的大罢工，控诉选举存在舞弊，奥廷加还将于8月15日宣布下一步行动计划。罢工的参与情况会因地而异，在国家超级联盟的地盘会引起较大混乱；考虑到执政党支持者不会参与罢工，所以全国大部分地方仍会保持正常形势。背景与分析建议请确认8月14日的所有商业及交通预约，特别是在反对派势力较强的地区。避开所有的抗议活动、政治集会，勿前往政府大楼、选举机构和安全管控区。出行时如见人群聚集，请即寻找安全场所躲避。密切跟踪、详细了解当地安全形势和交通状况，合理安排出行及货运。遇有路障，切勿通过。<br/>联系方式：<br/>所属国家：肯尼亚<br/>当地报警电话：112或999<br/>当地大使馆电话：+254-0719235543<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502957185000,"ftype":"","freaded":"","noticeId":"","title":"肯尼亚选后形势（更新11）","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1433","category":"安保","createDate":"2017-08-17 16:06:25"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述肯大选结果公布后，在反对党全国超级联盟的重要阵地——霍马湾、米戈利、基苏木和夏亚等郡——抗议活动和枪击行为持续进行，抗议者多采取点火和公开挑衅的形式，也有人使用枪支。警方已在这些地区加强了安全部署和直升机巡逻力量。建议由于反对党支持者及奥廷加本人均表示对选举结果不满，肯尼亚近期安全态势不稳，尤其是各种抗议活动频发。请在敏感期内避开抗议地点、人群聚集区、政党办公处、大规模集会和安保措施严密区域。根据安全局势制定应急计划，以备不时之需。准备一部卫星电话可在紧急情况下使用。如果附近发生骚乱或人群开始聚集，请迅速离开，前往安全的民宅躲避同时报警。请与使馆保持联系。<br/>联系方式：<br/>所属国家：肯尼亚<br/>当地报警电话：112或999<br/>当地大使馆电话：+254-0719235543<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502957159000,"ftype":"","freaded":"","noticeId":"","title":"（中交集团）肯尼亚大选后抗议活动多发地","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1432","category":"安保","createDate":"2017-08-17 16:05:59"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法机构驳回修宪案后，总统以欺诈的方式通过公投批准了修宪案。反对派成员深恐总统会再次修宪，不断巩固自身权力。现行宪法不允许阿齐兹在2019年期满后再次竞选。现任总统阿齐兹在2008年军事政变中胜出上台，2009年在总统选举中获胜，2014年在反对党抵制中再次当选。建议因存在暴力风险，请避开抗议活动。在集会地点周边时，请保持高度警惕。如果周围出现骚乱，请即离开躲避。出行前请了解交通状况，城区出行时请预留充足时间。<br/>联系方式：<br/>所属国家：毛里塔尼亚<br/>当地报警电话：17<br/>当地大使馆电话：00222-46586507、45252070<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502954575000,"ftype":"","freaded":"","noticeId":"","title":"毛里塔尼亚可能爆发全国性抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1431","category":"安保","createDate":"2017-08-17 15:22:55"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法机构驳回修宪案后，总统以欺诈的方式通过公投批准了修宪案。反对派成员深恐总统会再次修宪，不断巩固自身权力。现行宪法不允许阿齐兹在2019年期满后再次竞选。现任总统阿齐兹在2008年军事政变中胜出上台，2009年在总统选举中获胜，2014年在反对党抵制中再次当选。建议因存在暴力风险，请避开抗议活动。在集会地点周边时，请保持高度警惕。如果周围出现骚乱，请即离开躲避。出行前请了解交通状况，城区出行时请预留充足时间。<br/>联系方式：<br/>所属国家：毛里塔尼亚<br/>当地报警电话：17<br/>当地大使馆电话：00222-46586507、45252070<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502954572000,"ftype":"","freaded":"","noticeId":"","title":"毛里塔尼亚可能爆发全国性抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1430","category":"安保","creat