<style scoped>
.tmContactBox{text-align: left;background: #fff;height:100%;}
 .tmContactBox .avatar{
   width: 0.4rem;
   height: 0.4rem;
   top:0.12rem !important;
 }
</style>
<style>
  .tmContactBox .mint-cell-text ,.tmContactBox .mint-cell-label{
    padding-left: 10px;
  }
  .contactList  .mint-loadmore{height:100%;}
</style>
<template>
  <transition name="slide">
    <div class="tmContactBox">
      <div @click="addContact">
        <mt-cell title="添加手机联系人" is-link></mt-cell>
      </div>
      <div class="no-data-pub" v-if="contact.length<=0">
        <i class="iconfont  icon-zanwuxiaoxi"></i>
        <p class="text">团队通讯录还是空的</p>
        <p class="sub-text">点击“添加手机联系人”试试吧</p>
      </div>
      <div v-else>
        <mt-loadmore :top-method="loadTop" ref="loadmore">
          <div class="contactList">
            <mt-cell-swipe v-for="(item ,index) in contact"
                           :title=item.name
                           :label=item.phone.toString()
                           :right="[
                      {
                        content: '编辑',
                        style: { background: '#aeaeae', color: '#fff'},
                        handler: () => updateContact(item.phone,item.name)
                      },
                      {
                        content: '删除',
                        style: { background: '#e5433a', color: '#fff' },
                        handler: () => delContact(index,item.id)
                      }
                    ]">
              <img slot="icon" v-bind:src="item.headIconUrl | NoImg" class="avatar">
            </mt-cell-swipe>
          </div>
        </mt-loadmore>
      </div>
    </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'teamManage',
    data () {
      return {
        loginUser:{},
        contact:{}
      }
    },
    created:function(){
      tmSetTab(2);
//      this.loginUser=LoginUser.getUser();
      this.contactList(0,1000);
    },
    activated:function(){
      tmSetTab(2);
//      this.loginUser=LoginUser.getUser();
      this.contactList(0,1000);
    },
    methods:{
      loadTop:function() {
        this.contactList(0,1000);
        this.$refs.loadmore.onTopLoaded();
      },
      contactList:function (pageNum,pageSize) {
        let it = this;
        //通讯录列表
//        this.$axios({
//          method: 'post',
//          url: '/contact/getContactlist',
//          data:this.$qs.stringify({
//            userId:it.loginUser.userId,
//            pageNum:pageNum,
//            pageSize:pageSize
//          })
//        }).then(function (response) {
          var response = {"code":1,"msg":"success","data":{"total":5,"list":[
            {"id":79,"name":"测试号1","phone":15033336666,"headIconUrl":"http://img4q.duitang.com/uploads/item/201504/12/20150412H4242_MCfHm.thumb.224_0.jpeg"},
            {"id":80,"name":"测试账号2","phone":13310881088,"headIconUrl":"http://diy.qqjay.com/u2/2014/1012/080cd8a0d4e80ae4f028277ea93860e5.jpg"},
            {"id":81,"name":"测试账号1","phone":17600801597, "headIconUrl":"http://img4.imgtn.bdimg.com/it/u=1195374687,1150101672&fm=214&gp=0.jpg"},
            {"id":86,"name":"催涛","phone":17710872663, "headIconUrl":"http://diy.qqjay.com/u2/2012/0913/558db280d657ecefec1dfa45430a86ec.jpg"},
            {"id":88,"name":"刘培","phone":18810778626,"headIconUrl":"http://diy.qqjay.com/u/files/2013/0306/3582dc93956e814b662dcd24dd5d3541.jpg"}],"pageNum":0,"pageSize":1000,"pages":1,"size":5}};
          it.contact = response.data.list;
        //});
      },
      updateContact:function (phone,contactName) {
        this.$router.push({path: '/updateContact', query: { phone:phone,contactName:contactName }});
      },
      addContact:function(){
        appMethod = this.updateList;
        window.location.href="myapp://parameter?type=AddContactToMember&method=addContact"
      },
      updateList:function(){
        this.contactList(0,1000);
      },
      delwiContact:function (idx,id) {
        //通讯录删除
        let it = this;
        MessageBox.confirm('确定执行此操作?').then(action => {
          this.$axios({
            method: 'post',
            url: 'contact/deleteContacts',
            data:this.$qs.stringify({
              userId:it.loginUser.userId,
              id:id
            })
          }).then(function (response) {

            if(response.data.code >= 0){
              it.contact = it.contact.slice(0,idx).concat(it.contact.slice(idx+1,it.contact.length));
            }
          });
        });
      }
    },
    components:[Loadmore]
  }
</script>




