<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow: hidden;
    background-color: #f0f0f0;
  }
  .notices .box{
    padding: 0.2rem 0.15rem;
    margin-top: 0.48rem;
    background-color: #fff;
    position: relative;
  }
  .msgTitle{
     font-size: 1.2em;
     padding-bottom: 10px;
  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .msgContent{
     color: #666;
     position: relative;
  }
  .msgContent span{
    display: inline-block;
    width: 86vw;
    word-wrap:break-word;
    padding-left: 0.3rem;
  }
  .notices .icon-xiaoxi{
     position: absolute;
     top: -0.03rem;
     display: block;
     width: 0.18rem;
     height: 0.18rem;
     background-image: url("./xiaoxi.png");
     background-size: 100% 100%;
  }
  .msg_btn{
    padding: 0px 20px;
    height: 26px;
    line-height: 26px;
    border-radius: 13px;
    float: right;
    background:#fff;
    border:solid 1px #ffa908;
    color: #ffa908;
  }
  .sign{
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,182,10,0.8)), to(#ff6a01));
    color: #fff;
    border:none;
  }
  .msg_sign{
    padding-bottom: 10px;
  }
  .msg_sign span label{
    color: #666;
  }
  .msg_sign span i{
     display: inline-block;
     width: 0.2rem;
     height:0.2rem;
     background-image: url("./sign.png");
     background-size: 100% 100%;
     position: relative;
     top:3px;
     margin-right: 0.05rem;
  }
  .hdBody{height: calc(100vh - 60px - 41px);}
</style>
<template>
  <!--<transition name="slide">-->
  <div class="msgBody">
    <mt-header title="团队通知">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="no-data-pub" v-if="noticeList.length<=0">
      <i class="iconfont  icon-zanwuxiaoxi"></i>
      <p class="text">暂无团队通知信息</p>
      <p class="sub-text">给团队发通知时请点下面按钮</p>
    </div>
    <div class="notices hdBody" v-else>
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <div class="box" @click="clickList(idx)" v-for="(notice,idx) in noticeList">
             <div><label>{{notice.createTime|FormartDate}}</label></div>
             <div class="msgTitle">
               {{notice.ftitle}}
             </div>
              <div class="msg_sign">
                <span>发起人：{{notice.fuserName}}</span>
              </div>
             <div class="msgContent">
               <i class="icon-xiaoxi"></i>
               <span>{{notice.fcontent}}</span>
             </div>
             <span class="red-point" v-if="notice.readed !== true"></span>
          </div>
        </div>
        </mt-loadmore>
    </div>
    <div class="send-btn">
      <!--<router-link to="/newsRelease">-->
        <mt-button type="primary" size="large" @click="sendNotice('/home/sendNewsRelease')">发送团队通知</mt-button>
      <!--</router-link>-->
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'teamNotice',
    data () {
      return {
         pageNum:1,
         pageSize:20,
         allLoaded:false,
         noticeList:[]
      }
    },
    components:[ Loadmore ],
    activated:function(){
      this.pageNum = 1;
      this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        this.$router.goBack();
      },
      sendNotice:function(url){
        this.$router.push(url);
      },
      clickList:function(idx){
        this.$router.push({path: '/home/teamNoticeInfo', query: { id:this.noticeList[idx].id }});
      },
      queryList:function(){//查询团队通知列
         let _this = this;
//         this.$axios({
//            method: 'post',
//            url: '/notice/getPageListOfApp',
//            data:this.$qs.stringify({
//               userId:LoginUser.getUser().userId,
//               pageNum:this.pageNum,
//               pageSize:this.pageSize
//            })
//         }).then(function (response) {
            let response = {"code":1,"msg":"success","data":{"total":2,"list":[{"id":20,"ffromUserId":"","ftoGroupId":"","ftitle":"通知","fcontent":"通知","fcreateAt":"","imageUrl":"","count":0,"fuserName":"杨勇冠","createTime":"2017-09-04 17:29:33","messageId":112142,"readed":true,"sourceImagUrl":"","nailImagUrl":"","groupName":""},{"id":19,"ffromUserId":"","ftoGroupId":"","ftitle":"鹅鹅鹅 ","fcontent":"额","fcreateAt":"","imageUrl":"","count":0,"fuserName":"杨勇冠","createTime":"2017-09-04 17:27:14","messageId":112137,"readed":true,"sourceImagUrl":"","nailImagUrl":"","groupName":""}],"pageNum":1,"pageSize":20,"pages":1,"size":2}};
            if(response.msg=='success'){
              if(!response.data.list||response.data.list.length<=0){
                _this.allLoaded = true;
              }
              if(_this.pageNum===1){
                _this.noticeList = response.data.list;
              }else{
                _this.noticeList = _this.noticeList.concat(response.data.list);
              }
            }
            if(!_this.$refs.loadmore) return;
            _this.$refs.loadmore.onBottomLoaded();
            _this.postion();
        // });
      }
    }
  }
</script>




