      },
      initData:function(){
        let res = {"code":1,"msg":"success","data":{"total":126,"list":[{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月17日16时50分许，巴塞罗那兰布拉大街发生重大汽车冲撞恐袭事件，当局在随后的行动中已拘捕两名嫌犯。袭击所用的货车的司机弃车逃跑，至今逍遥法外。袭击已造成至少13人死亡，100人受伤，死亡人数很可能还会上升。早先另有报道称，恐袭后不久，兰布拉大街与Carrer Nou de la Rambla拐角处一餐馆发生劫持人质事件，现证实为假消息。背景与分析当局已将袭击定性为恐袭，并已启动一个危机响应小组负责后续行动。“伊斯兰国”已通过其阿玛克通讯社(Aamaq news agency)声称对袭击负责，当局目前还未能证实这一说法是否属实。官方已宣布加泰罗尼亚自治区哀悼3天，并将于8月18日中午12点在巴塞罗那中心广场举行默哀仪式。建议官方完成调查前，请远离恐袭现场。预计巴塞罗那埃尔普拉特国际机场安保森严，请预留更多安检时间。由于巴塞罗那市已实施警方管制，如非必要，切勿外出。警方势必会根据嫌疑人口供展开突袭行动，围捕可能的恐袭同谋。在当局持续搜捕嫌疑人的行动过程中，部分区域或疏散。要听从地方当局的指令。如遇安全人员上前盘问，请礼貌配合。切勿拍摄警方行动。扩展阅读 交通：com-shi-va.lameva.barcelona.cat 巴塞罗那市交通：www.tmb.cat 巴塞罗那埃尔普拉特国际机场：www.barcelona-airport.com<br/>联系方式：<br/>所属国家：西班牙<br/>当地报警电话：091<br/>当地大使馆电话：+34-699089086<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1503278641000,"ftype":"","freaded":"","noticeId":"","title":"巴塞罗那恐袭(更新3)","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1456","category":"安保","createDate":"2017-08-21 09:24:01"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述日本政府宣布，6架美军“鱼鹰”运输机将参加8月18日的美日军事演习。针对此事，活跃分子会举行抗议活动。军事演习将于8月28日结束，作为演习的一部分，“鱼鹰”运输机将从青森县三泽美军机场机动至北海道的两个演习地点。这些“鱼鹰”运输机驻扎在冲绳县普天间飞行场（MCAS）。背景与分析许多日本人认为“鱼鹰”运输机不安全。日本此前要求美国停止使用“鱼鹰”运输机，但据报道，美国拒绝这一要求，称飞机很安全。鉴此，日本政府同意使用“鱼鹰”运输机会被活跃分子视为对美国的妥协。大多数美国军队驻扎在冲绳，因此冲绳市民对政府这一举动表示强烈反对。近年来，因政府计划将普天间飞行场迁往冲绳北部的新基地，冲绳形势持续紧张，部署“鱼鹰”则是火上浇油。2016年12月，一架在冲绳外海执行训练飞行的“鱼鹰”机迫降解体，虽无人员伤亡，但仍引起当地民众的愤怒。事故发生后，美国一度暂停使用“鱼鹰”机，后经安全检查后复飞。建议避开示威活动。在集会地点周边时应提高警惕。在东京和冲绳出行前，请了解最新路况信息。建议绕行，避开集会地点。夜间和周末出行时请预留充足时间。<br/>联系方式：<br/>所属国家：日本<br/>当地报警电话：110<br/>当地大使馆电话：+81-03-3403-3064或+81-03-3403-3065<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1503021529000,"ftype":"","freaded":"","noticeId":"","title":"日本民众抗议美军在日使用“鱼鹰”运输机（更新1）","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1453","category":"安保","createDate":"2017-08-18 09:58:49"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法机构驳回修宪案后，总统以欺诈的方式通过公投批准了修宪案。反对派成员深恐总统会再次修宪，不断巩固自身权力。现行宪法不允许阿齐兹在2019年期满后再次竞选。现任总统阿齐兹在2008年军事政变中胜出上台，2009年在总统选举中获胜，2014年在反对党抵制中再次当选。建议因存在暴力风险，请避开抗议活动。在集会地点周边时，请保持高度警惕。如果周围出现骚乱，请即离开躲避。出行前请了解交通状况，城区出行时请预留充足时间。<br/>联系方式：<br/>所属国家：毛里塔尼亚<br/>当地报警电话：17<br/>当地大使馆电话：00222-46586507、45252070<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1503021248000,"ftype":"","freaded":"","noticeId":"","title":"毛里塔尼亚可能爆发全国性抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1442","category":"安保","createDate":"2017-08-18 09:54:08"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"建议不要前往伦敦桥和博罗市场区域，直到政府完成伤员疏散并确认现场安全。听众执法部门人员的所有指令；如果警察上前盘问，保持礼貌和合作。至少一直到6月4日中午，要对包括地面和地铁出行延误在内的局部地区出行中断做好准备。综述6月4日早晨，英国政府继续应对伦敦发生的数起重大事件。6月3日晚上22点刚过，第一起事件发生，一辆高速行驶的小货车冲上伦敦桥的人行道并撞击了大约15至20名行人。目击者称，他们看到有2到4人在伦敦桥袭击后下了车，然后向附近的博罗市场跑去。几分钟后，伦敦警察厅(the Metropolitan Police)确认，警察已经对博罗市场持刀伤人的消息作出反应，并且在随后数次开枪。两处现场都报告有人伤亡，初步信息显示，有数人死亡，另有超过20人受伤。关于凶手身份的细节信息还没有立即披露。英国政府已经宣布此次事件为恐怖袭击；但是，同一时间邻近的沃克斯豪尔区(Vauxhall area)报告的持刀伤人事件已经被证明与这次恐怖袭击无关。<br/>联系方式：<br/>所属国家：英国<br/>当地报警电话：112或999<br/>当地大使馆电话：+44-020-76311430<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1503019702000,"ftype":"","freaded":"","noticeId":"","title":"6月3日至4日英国伦敦车辆伤人事件(第2次更新)","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1441","category":"安保","createDate":"2017-08-18 09:28:22"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法机构驳回修宪案后，总统以欺诈的方式通过公投批准了修宪案。反对派成员深恐总统会再次修宪，不断巩固自身权力。现行宪法不允许阿齐兹在2019年期满后再次竞选。现任总统阿齐兹在2008年军事政变中胜出上台，2009年在总统选举中获胜，2014年在反对党抵制中再次当选。建议因存在暴力风险，请避开抗议活动。在集会地点周边时，请保持高度警惕。如果周围出现骚乱，请即离开躲避。出行前请了解交通状况，城区出行时请预留充足时间。<br/>联系方式：<br/>所属国家：毛里塔尼亚<br/>当地报警电话：17<br/>当地大使馆电话：00222-46586507、45252070<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1503019512000,"ftype":"","freaded":"","noticeId":"","title":"毛里塔尼亚可能爆发全国性抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1440","category":"安保","createDate":"2017-08-18 09:25:12"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述肯尼亚选举委员会预计在8月11日中午宣布大选最终结果。在等待选举结果的过程中，许多地区爆发了抗议活动。在恩布、尼蒂、内罗毕和曼德拉郡，由于抗议者认为结果公布得太慢，多处投票点的工作被中断。建议肯尼亚政局不稳，选后期间随时可能发生变化。请避开抗议地点、人群聚集区尤其是投票点计票点、政党办公处、大规模集会和安保措施严密区域。如果附近发生骚乱或人群开始聚集，请迅速离开，前往安全的民宅躲避。出行前应确认沿路安全状况。制定应急计划，以备不时之需。储存足够的瓶装水和食物，以防局势长期不稳。准备一部卫星电话可在紧急情况下使用。<br/>联系方式：<br/>所属国家：肯尼亚<br/>当地报警电话：112或999<br/>当地大使馆电话：+254-0719235543<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1503019445000,"ftype":"","freaded":"","noticeId":"","title":"（中交集团）肯尼亚多处爆发抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1439","category":"安保","createDate":"2017-08-18 09:24:05"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"建议不要前往伦敦桥和博罗市场区域，直到政府完成伤员疏散并确认现场安全。听众执法部门人员的所有指令；如果警察上前盘问，保持礼貌和合作。至少一直到6月4日中午，要对包括地面和地铁出行延误在内的局部地区出行中断做好准备。综述6月4日早晨，英国政府继续应对伦敦发生的数起重大事件。6月3日晚上22点刚过，第一起事件发生，一辆高速行驶的小货车冲上伦敦桥的人行道并撞击了大约15至20名行人。目击者称，他们看到有2到4人在伦敦桥袭击后下了车，然后向附近的博罗市场跑去。几分钟后，伦敦警察厅(the Metropolitan Police)确认，警察已经对博罗市场持刀伤人的消息作出反应，并且在随后数次开枪。两处现场都报告有人伤亡，初步信息显示，有数人死亡，另有超过20人受伤。关于凶手身份的细节信息还没有立即披露。英国政府已经宣布此次事件为恐怖袭击；但是，同一时间邻近的沃克斯豪尔区(Vauxhall area)报告的持刀伤人事件已经被证明与这次恐怖袭击无关。<br/>联系方式：<br/>所属国家：英国<br/>当地报警电话：112或999<br/>当地大使馆电话：+44-020-76311430<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502963888000,"ftype":"","freaded":"","noticeId":"","title":"6月3日至4日英国伦敦车辆伤人事件(第2次更新)","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1438","category":"安保","createDate":"2017-08-17 17:58:08"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法机构驳回修宪案后，总统以欺诈的方式通过公投批准了修宪案。反对派成员深恐总统会再次修宪，不断巩固自身权力。现行宪法不允许阿齐兹在2019年期满后再次竞选。现任总统阿齐兹在2008年军事政变中胜出上台，2009年在总统选举中获胜，2014年在反对党抵制中再次当选。建议因存在暴力风险，请避开抗议活动。在集会地点周边时，请保持高度警惕。如果周围出现骚乱，请即离开躲避。出行前请了解交通状况，城区出行时请预留充足时间。<br/>联系方式：<br/>所属国家：毛里塔尼亚<br/>当地报警电话：17<br/>当地大使馆电话：00222-46586507、45252070<br/>当地全球领事保护电话：+86-10-12308或+86-10-59913991<br/>","fcreateAt":1502963856000,"ftype":"","freaded":"","noticeId":"","title":"毛里塔尼亚可能爆发全国性抗议活动","isPublish":"","isDelete":"","ftoGroupIds":"","ftoGroupsStr":"","date":"","ftypeStr":"","count":0,"publishUserName":"","warningInfoId":"1437","category":"安保","createDate":"2017-08-17 17:57:36"},{"id":"","ffromUserId":"","ftoUserId":"","fcontent":"综述8月15日晚，毛里塔尼亚总统阿齐兹正式签署了取消参议院的修宪案，未来数天，反对派人士可能举行抗议。修宪案的签署意味着8月5日公投日那天起立法机构即已解散。抗议活动的具体时间和地点不会提前公布，很可能在该国南部城市努瓦迪布、努瓦克肖特和祖埃拉特举行。在努瓦克肖特，反政府示威活动通常在总统府、国会大楼、综合性医院和马德里路口（Madrid Intersection）周边举行。背景与分析7月份和8月份，政党和公民社会团体联盟“别碰我的宪法”（\"Touche pas ma Constitution\"）运动在毛里塔尼亚数个城市举行集会和游行活动，抵制修宪公投。警方频频强行驱散示威者，引发冲突，抗议者和警察均有受伤。依据修宪案，高等法院被解散，参议院由地方政府取代。阿齐兹的批评者称，在立法