        this.warningList = res.data.list;
      },
      getTdNum:function(ftype){
        let _this=this;
        this.$axios({
          method: 'post',
          url: '/tmessage/countMessages',
          data:this.$qs.stringify({userId:_this.loginUser.userId,ftype:ftype})
        }).then(function (response) {
          if(ftype=='2'){
            _this.teamMesgNum.tdqz = response.data.data;
          }else if(ftype=='0'){
            _this.teamMesgNum.tdtz = response.data.data;
          }else if(ftype=='7'){
            _this.teamMesgNum.tdqd = response.data.data;
          }
        });
      },
      locationSharing:function(){
        let _this=this;
        this.$axios({
          method: 'post',
          url: '/tlocation/countUserOpenGroup',
          data:this.$qs.stringify({userId:_this.loginUser.userId})
        }).then(function (response) {
          _this.locationSharingNum = response.data.num;
        });
      },
      getXcNum:function(){
        let _this=this;
        this.$axios({
          method: 'post',
          url: 'ttravel/selectCount',
          data:this.$qs.stringify({userId:_this.loginUser.userId})
        }).then(function (response) {
          _this.mesgNum=response.data.data;
        });
      },
      scnaQRpanduan:function () {
        if(this.loginUser.isBang === 1){
          this.scnaQR = false;
        }else if(this.loginUser.isBang === 0){
          this.scnaQR = true;
        }
      },
      closeModal:function(){
        this.scnaQR = false;
      },
      clickNews:function(idx){//点击轮播预警信息
        this.$router.push('/home/warningDet?warningInfoId='+this.warningList[idx].warningInfoId);
      },
      enterPostion:function(){//进入位置服务
        numMethod = this.initDataNumber;
        window.location.href = 'myapp://parameter?type=SharedLocation&method=appCallBack';
      },
      scnaQRcode:function () {
        scnaQRcodeMethod = this.getQRcodeData;
        /* 发送伪链接，调用手机扫描二维码 */
        window.location.href = 'myapp://parameter?type=QrCodeScanning&method=callbackQRcode';
      },
      getQRcodeData:function(data){
        localStorage.setItem('twocData',JSON.stringify(data));
        this.scnaQR = false;
        this.$router.push('/apparatus?isHome=true&Mac='+data.data.Mac
          +'&WifiName='+data.data.WifiName+'&WifiPwd='+data.data.WifiPwd);
      },
      goExponent:function () {
        let instance = Toast("此功能正在构建中");
        setTimeout(() => {
          instance.close();
      }, 1000);
      }
    }
  }
</script>




