<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'teamHelp',
    data () {
      return {
        pageNum:1,
        pageSize:20,
        allLoaded:false,
        msgList: []
      }
    },
    components:[ Loadmore ],
    activated:function(){
      this.pageNum = 1;
      this.queryList();
    },
    methods:{
      backPage:function(){
        this.$router.goBack();
      },
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmoreHelp.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            if(!dom) return;
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
