          if(response.msg=='success'){
            if(!response.data.list||response.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.msgList = response.data.list;
            }else{
              _this.msgList = _this.msgList.concat(response.data.list);
            }
          }
          if(!_this.$refs.loadmoreHelp) return;
          _this.$refs.loadmoreHelp.onBottomLoaded();
          _this.postion();

